.numbers {
    padding-top: 25px;
    padding-bottom: 27px;
    background: #F6F6F6;
    @media (min-width: 768px) {
        padding-top: 43px;
        padding-bottom: 54px;
    }
}
.numbers__container {
    display: flex;
    margin-bottom: 23px;
    @media (min-width: 576px) {
        margin-bottom: 0;
    }
}
.number__image {
    width: 275px;
    height: 334px;
    position: absolute;
    top: -30px;
    .icon {
        width: 100%;
        height: 100%;
    }
}
.number__num {
    font-family: 'bebasneue';
    text-transform: uppercase;
    color: #D1A788; 
    height: 64px;
    min-width: 79px;
    font-weight: 300;
    font-size: 80px;
    line-height: 64px;
    position: relative;
    margin-right: 19px;
    @media (min-width: 768px) {
        height: 115px;    
        min-width: 141px;
        font-size: 150px;
        line-height: 114px;
    }
    &::before {
        content: 'лет';
        position: absolute;
        right: 0;
        bottom: 7px;
        font-family: inherit;
        font-weight: 400;
        font-size: 18px;
        line-height: 18px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: inherit;
        transform: rotate(-90deg);
        @media (min-width: 768px) {
            font-size: 24px;
            bottom: 10px;
            line-height: 24px;
        }
    }
}
.number__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: 0.01em;
    color: #000000;
    margin-bottom: 5px;
    @media (min-width: 768px) {
        font-size: 22px;
        margin-bottom: 8px;
    }
    span {
        display: block;
        font-weight: 300;
        font-size: 13px;
        line-height: 130%;
        letter-spacing: 0.01em;
        color: #999999;
    }
}